/**
 * @pageName ProductPage
 * @slotType site plugin
 */
export type CheckoutHeaderProps = {
  // The ID of the current checkout process.
  checkoutId: string;

  // The ID of the current checkout step: `contact-details`, `delivery-method` or `payment-and-place-order`
  stepId: CheckoutStepId;

  // An event handler that accepts a callback function that is invoked by a widget. The widget should call the function whenever the checkout needs to be refreshed.
  onRefreshCheckout: (refreshCheckoutCallback: () => void) => void;
};
/** @internal */
export type CheckoutStepId =
  | 'contact-details'
  | 'delivery-method'
  | 'payment-and-place-order';

